@import url(https://fonts.googleapis.com/css2?family=Reenie+Beanie&display=swap);
/* Global Styles */
body {
  margin: 0;
  overflow: hidden;
}

/* width */
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #dc3545;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #9e2733;
}

.isDraggableContainer {
  pointer-events: none;
  opacity: 0.6;
}

.noBorderRadius {
  border-radius: 0 !important;
}

#twitch-embed {
  margin-bottom: -4px;
}

.MuiOutlinedInput-multiline {
  height: 100%;
  align-items: start !important;
  overflow: hidden;
}

.flexContainer {
  display: flex;
}
.greyText {
  color: rgba(0, 0, 0, 0.54);
}

.twitchLiveTextColor {
  color: #772ce8;
}

.textShadow {
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
}

.miniDash-btnContainer {
  width: 25%;
  position: relative;
}

.insultText {
  font-family: 'Reenie Beanie', cursive;
  color: white;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
  font-size: 20pt;
  width: 30%;
  text-align: center;
  margin-top: 5vh;
}

@media (max-width: 1200px) {
  .miniDash-btnContainer {
    width: 30%;
  }
}
@media (max-width: 600px) {
  .miniDash-btnContainer {
    width: 40%;
  }
}

.animatedBolt {
  animation: spin 1400ms infinite ease-in-out;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.stream {
  position: relative;
  display: inline-block;
  background-color: #9147ff;
}

.stream:before,
.stream:after {
  content: '';
  display: block;
  background-color: #9147ff;
  width: 8px;
  height: 8px;
  position: absolute;
  transition: all 0.15s ease;
}

.stream:before {
  top: 0;
  left: 0;
  transform-origin: top left;
  transform: rotate(-45deg) scale(0);
}

.stream:after {
  right: 0;
  bottom: 0;
  transform-origin: bottom right;
  transform: rotate(45deg) scale(0);
}

.stream__thumbnail {
  display: block;
  transform: translate(0, 0);
  transition: all 0.15s ease;
  position: relative;
  z-index: 10;
}

.stream:hover .stream__thumbnail {
  transform: translate(5px, -5px);
  cursor: pointer;
}

.stream:hover:before {
  transform: rotate(-45deg) scale(1);
}

.stream:hover:after {
  transform: rotate(45deg) scale(1);
}

